import React, { useState } from 'react';
import Slide from "./slide"
import Slider from "react-slick"
import Button from "./button"
import data from "../data.json"
// import CarouselIndicators from "../../Common/carouselIndicators"
import {
  Carousel,
  CarouselControl,
  CarouselIndicators,
  CarouselItem
} from 'reactstrap';

const HomeCarousel = () => {
  const { carousel } = data
  var borderStyle = {
    borderBottom: "2px solid white",
  }

  var settings = {
    arrows: true,
    centerMode: false,
    centerPadding: "40px",
    dots: false,
    infinite: true,
    slidesToShow: 4,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 769,
        settings: {
          dots: false,
          centerMode: true,
          slidesToShow: 3,
        },
      },
      {
        breakpoint: 577,
        settings: {
          dots: true,
          slidesToShow: 1,
        },
      },
    ],
  }

  // Carouse slides

  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === carousel.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  }

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? carousel.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  }

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  }

  const slides = carousel.map((carouselItem) => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={carouselItem.src}
      >
        <picture>
          <source
            className="relative-position"
            media="(max-width: 425px)"
            srcSet={carouselItem.responsiveImage}
          />
          <img
            className="img-fluid"
            src={carouselItem.image}
            alt={carouselItem.alt}
          />
        </picture>
        <div className="carousel-caption col-9 col-xl-6 col-md-8 text-md-left text-center mt-md-5">
          <p className="font-lg absolute-caption neosans-light text-white mb-0">
            {carouselItem.firstLine}
            <br /> {carouselItem.secondLine}
            <br />
            <a
              className="banner-link text-white"
              href={carouselItem.link}
              target="_blank"
              rel="noopener noreferrer"
              style={borderStyle}
            >
              <span className="font-lg maison-bold py-1">{carouselItem.linkText}</span>
            </a>
          </p>
        </div>
      </CarouselItem>
    )
  });

  // Carousel slides

  return (
    <section className="first-section">
      {/* Desktop Carousel */}
      <Carousel
        className="carousel d-none d-md-block go-jek-slider"
        id="go-jek-slider"
        activeIndex={activeIndex}
        next={next}
        previous={previous}
      >
        <div className="d-none d-md-block indicators-align">
          <CarouselIndicators
            className="carousel-indicators homepage-carousel-indicators  mx-0 col-xl-2 col-lg-2 col-md-3 ml-lg-5 ml-xl-5 mb-xl-5 pt-lg-4 justify-content-end"
            items={carousel}
            activeIndex={activeIndex}
            onClickHandler={goToIndex}
          />
        </div>
        {slides}
        <div className="carousel-caption py-0 carousel-btn text-uppercase">
          <Button
            type="outline"
            text="VIEW OPEN POSITIONS"
            link="/all-open-positions/"
            position="left"
          />
        </div>
      </Carousel>

      {/* End Desktop view */}

      {/* Mobile carousel */}
      <Slider
        {...settings}
        className="row home-slider d-md-none d-block text-center"
      >
        {carousel.map((carouselItem, key) => (
          <Slide data={carouselItem} key={key} />
        ))}
      </Slider>
    </section>
  )
}

export default HomeCarousel
